// import {DocumentEmitter} from './document-emitter.js';

/*
* animation
* breakpoints
* autoSlide
* startSeparatorPosition
* touch support
* light gallery
* mouseWheel - ?
* video - ?
* vertical etc
* */
// (function() {
    // settings
    var SCROLL_DISABLE_CLASS_NAME = 'stop-scroll';

    var PLUGIN_CSS_NAME = 'before-after';
    var CONTAINER_CLASS_NAME = PLUGIN_CSS_NAME + '-container';
    var BEFORE_WRAPPER_CLASS_NAME = PLUGIN_CSS_NAME + '-wrapper-before';
    var AFTER_ELEMENT_CLASS_NAME = PLUGIN_CSS_NAME + '-element-after';
    var SEPARATOR_ELEMENT_CLASS_NAME = PLUGIN_CSS_NAME + '-separator';
    var SEPARATOR_ACTIVE_CLASS_NAME = PLUGIN_CSS_NAME + '-active-separator';
    //

    function DocumentEmitter() {
        // emmitter
        this.eventsListeners = {};

        this.on = function(name, listener) {
            if (!this.eventsListeners[name]) {
                this.eventsListeners[name] = [];
            }

            this.eventsListeners[name].push(listener);
        }
        this.removeOn = function(name, listenerToRemove) {
            if (!this.eventsListeners[name]) return;

            function filterListeners(listener) {
                return listener !== listenerToRemove;
            }

            this.eventsListeners[name] = this.eventsListeners[name].filter(filterListeners);
        }
        this.emit = function(name, data) {
            if (!this.eventsListeners[name]) return;

            function fireCallbacks(callback){
                callback(data);
            }

            this.eventsListeners[name].forEach(fireCallbacks);
        }
        // emmitter

        function resize() {
            this.emit('resize');
        }
        function mouseup(event) {
            this.emit('mouseup', event);
        }
        function mousedown(event) {
            this.emit('mousedown', event);
        }
        function mousemove(event) {
            this.emit('mousemove', event);
        }
        document.addEventListener('mousedown', mousedown.bind(this));
        document.addEventListener('mouseup', mouseup.bind(this));
        document.addEventListener('mousemove', mousemove.bind(this));

        document.addEventListener('touchstart', mousedown.bind(this));
        document.addEventListener('touchend', mouseup.bind(this));
        document.addEventListener('touchcancel', mouseup.bind(this));
        document.addEventListener('touchmove', mousemove.bind(this));

        window.addEventListener('resize', resize.bind(this));
    };

    var $doc = new DocumentEmitter;

    function BeforeAfter() {
        this.container = null;

        this.beforeWrapper = null;
        this.afterWrapper = null;

        this.separatorElement = null;
        this.separatorChildren = null;

        this.mouseScreenX = null;

        this.eventsListeners = {};

        var defaults = {
            test: true,
            freePosition: false,
            init: true,
            separator: {
                activeClass: SEPARATOR_ACTIVE_CLASS_NAME,
            },
        };

        if (arguments[0] && typeof arguments[0] === "object") {
            this.options = extendDefaults(defaults, arguments[0]);
        }

        var mouseMoveHandlerBind = mouseMoveHandler.bind(this);
        var $ = this;

        function mouseDownHandler(event) {
            console.log('down')
            var isSeparatorChildren = $.separatorChildren && $.separatorChildren.includes(event.target);
            if($.options.freePosition) {
                if(event.target === $.container || isSeparatorChildren || event.target === $.separator) {
                    mouseMoveHandlerBind(event)
                    $doc.on('mousemove', mouseMoveHandlerBind);
                }
            } else if(event.target === $.separator || isSeparatorChildren ) {
                $doc.on('mousemove', mouseMoveHandlerBind);
            }
        }
        function mouseUpHandler(event) {
            console.log('up')
            enableScroll();
            $doc.removeOn('mousemove', mouseMoveHandlerBind);
            $.separator.classList.remove($.options.separator.activeClass);
        }

        this.init = function() {

            buildOut.call($);
            if(!$.container) return sendError('container not found');
            setSizes.call($);

            $doc.on('mousedown', mouseDownHandler);
            $doc.on('mouseup', mouseUpHandler);
            $doc.on('resize', setSizes.bind($));
        }

        this.destroy = function() {
            // this.container = getElement(this.options.container) || false;
            if($.container) {
                $.container.innerHTML = '';
                $.container.removeAttribute('class');
                $doc.removeOn('mousedown', mouseDownHandler);
                $doc.removeOn('mouseup', mouseUpHandler);
                $doc.removeOn('resize', setSizes.bind($));
            }
            $.emit('destroy');
        }

        if(this.options.init) this.init();

    }

    // public fns

    // emmitter
    BeforeAfter.prototype.on = function(name, listener) {
        if (!this.eventsListeners[name]) {
            this.eventsListeners[name] = [];
        }

        this.eventsListeners[name].push(listener);
    }
    BeforeAfter.prototype.removeOn = function(name, listenerToRemove) {
        if (!this.eventsListeners[name]) {
            return sendError("Can't remove a listener. Event \"" + name + "\" doesn't exits.");
        }

        function filterListeners(listener) {
            return listener !== listenerToRemove;
        }

        this.eventsListeners[name] = this.eventsListeners[name].filter(filterListeners);
    }
    BeforeAfter.prototype.emit = function(name, data) {
        if (!this.eventsListeners[name]) return;

        function fireCallbacks(callback){
            callback(data);
        }

        this.eventsListeners[name].forEach(fireCallbacks);
    }

    // private fns
    function extendDefaults(source, properties) {
        for (var [key,value] of Object.entries(properties)) {
            if( typeof value === 'object' && !Array.isArray(value) && !value.nodeType ) {
                var obj = {};
                obj[key] = extendDefaults(source[key],properties[key]);
                return Object.assign({},source,obj);
            } else {
                source[key] = properties[key];
            }
        }
        return Object.assign({},source,properties);
    }

    function getElement(option) {
        if (option) {
            if(option.nodeType) {
                return option;
            } else if(typeof option === 'string') {
                return document.querySelector(arguments[0]);
            }
        }
        return null;
    }

    function buildOut() {
        this.container = getElement(this.options.container) || false;
        if(this.container) {
            this.container.className = CONTAINER_CLASS_NAME;
            if(this.options.className) this.container.className += ` ${this.options.className}`;

            this.beforeWrapper = document.createElement('DIV');
            this.beforeWrapper.className = BEFORE_WRAPPER_CLASS_NAME;

            this.beforeElement = document.createElement('IMG');
            this.beforeElement.src = this.container.getAttribute('data-before');
            this.beforeWrapper.append(this.beforeElement);

            this.afterElement = document.createElement('IMG');
            this.afterElement.src = this.container.getAttribute('data-after');
            this.afterElement.className = AFTER_ELEMENT_CLASS_NAME;

            this.separator = document.createElement('DIV');
            this.separator.className = SEPARATOR_ELEMENT_CLASS_NAME;
            if(this.options.separator && this.options.separator.class) this.separator.className += ` ${this.options.separator.class}`;
            if(this.options.separator && this.options.separator.innerHTML) this.separator.innerHTML += ` ${this.options.separator.innerHTML}`;
            this.container.append(this.beforeWrapper, this.afterElement, this.separator);

            if(this.options.separator.innerHTML) this.separatorChildren = Array.prototype.slice.call(this.separator.children);

            this.emit('buildOut');
        }
    }

    function setSizes() {
        if(this.options.width) this.container.style.width = this.options.width + 'px';
        if(this.options.height) this.container.style.height = this.options.height + 'px';

        this.beforeWrapper.style.cssText = `width: ${this.container.offsetWidth/2}px;`;
        this.beforeElement.style.width = this.container.offsetWidth + 'px';
        this.separator.style.cssText = `left: ${this.container.offsetWidth/2}px;`;
    }

    function mouseMoveHandler(event) {
        disableScroll();

        var posX;
        if(event.pageX) posX = event.pageX;
        if(event.touches) posX = event.touches[0].clientX;

        this.separator.classList.add(this.options.separator.activeClass);
        this.separator.style.cssText = (posX < this.container.offsetWidth) ? `left: ${posX}px;` : `left: ${this.container.offsetWidth}px;`;
        this.beforeWrapper.style.cssText = (posX > 0) ? `width: ${posX}px;` : 'width: 0px;';
    }

    function disableScroll() {
        document.documentElement.classList.add(SCROLL_DISABLE_CLASS_NAME);
    }

    function enableScroll() {
        document.documentElement.classList.remove(SCROLL_DISABLE_CLASS_NAME);
    }

    function sendError(message) {
        console.error('BeforeAfter Error: ' + message);
    }

// })()
export default  BeforeAfter;